import { Controller, CangjieInputEvent } from '@ali/4ever-cangjie';
import { BiPluginConfig } from '../../types'
import onInsertText from './handlers/onInsertText';
import onDelete from './handlers/onDelete';

export default function createCangjieInput(config: BiPluginConfig) {
  return (
    event: CangjieInputEvent,
    controller: Controller,
    next: Function,
  ) => {
    if (!config?.autoSpacing?.enable || !config.autoSpacing.enable()) return next();

    if (event.detail?.type === 'insertText') {
      return onInsertText(event, controller, next);
    } else if (event.detail?.type === 'deleteContentBackward') {
      return onDelete(controller, next);
    }
    return next();
  }
}